import React from 'react';
import s from "./../user.module.css"
import { useState } from 'react';
import cn from 'classnames'

export const UserPageChange = ({onPageChange,currentPage,totalUserCounter,pageSize, portionSize = 10}) => {
    let pagesCount = Math.ceil(totalUserCounter / pageSize); // share total users on current users on page

    let pages = [];
    for(let i=1;i < pagesCount; i++ ) {  
        pages.push(i);
    }

    let portionCount = Math.ceil(pagesCount / portionSize)
    let [portionNumber, setPortionNumber] = useState(1); // hook
    let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionPageNumber = portionNumber * portionSize;

    return (
        <div className={s.pageBox}>
            { portionNumber > 1 &&
            <button onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button> }
    
            {pages
            .filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
            .map((p) => {
                return <span className={ cn({ [s.selectedPage]: currentPage === p }, s.pageNumber) } key={p} onClick={(e) => { onPageChange(p) }}>{p}</span>
            })}

         { portionCount > portionNumber &&
                <button onClick={() => { setPortionNumber(portionNumber + 1) }}>NEXT</button> }
        </div>
    )
}